<template>
  <div id="app">
    <nav-bar :navbar="navbar" v-if="!$route.path.includes('admin')"></nav-bar>
    <transition name="slide-fade">
      <router-view :navbar="navbar"></router-view>
    </transition>
    <app-footer v-if="!$route.path.includes('admin')"></app-footer>
    <announcebar v-if="!$route.path.includes('admin')" :class="{ 'announce-bar--hidden': !showNavbar }"></announcebar>
  </div>
</template>

<style lang="scss">
@import './assets/sass/main.scss';
</style>

<script>
import $ from 'jquery';
import Announcebar from '@/components/Announcebar.vue';
import NavBar from '@/components/NavBar.vue';
import AppFooter from '@/components/Footer.vue';

export default {
  name: 'APP',
  components: {
    Announcebar,
    NavBar,
    AppFooter,
  },
  data() {
    return {
      showNavbar: false,
      lastScrollPosition: 0,
      navbar: [
        {
          cat: '',
          engCat: 'ALL COURSES',
          des: '',
          icon: 'fas fa-th-large',
        },
        {
          cat: '',
          engCat: 'Aerobic course',
          des: 'easy',
          icon: 'fas fa-bowling-ball',
        },
        {
          cat: '',
          engCat: 'Yoga course',
          des: 'OL',
          icon: 'fas fa-pray',
        },

        {
          cat: '',
          engCat: 'Swimming course',
          des: '',
          icon: 'fas fa-swimmer',
        },
        {
          cat: '',
          engCat: 'Spinning bike course',
          des: '',
          icon: 'fas fa-bicycle',
        },
        {
          cat: '',
          engCat: 'Others',
          des: '...',
          icon: 'fas fa-dumbbell',
        },
      ],
    };
  },
  // 
  watch: {
    $route() {
      $('.navigation').addClass('navigation-dark');
      this.showNavbar = false;
    },
  },
  methods: {
    // navbar
    scrollManNav() {
      if (this.$route.path === '/') {
        if ($(window).scrollTop() > 200) {
          $('.navigation').addClass('navigation-dark');
        } else {
          $('.navigation').removeClass('navigation-dark');
        }
      }
    },
    scrollAnnounce() {
      const currentScrollPosition = window.pageYOffset || document.documentElement.scrollTop;
      if (currentScrollPosition < 60) {
        return;
      }
      if (Math.abs(currentScrollPosition - this.lastScrollPosition) < 60) {
        return;
      }
      this.showNavbar = currentScrollPosition < this.lastScrollPosition;
      this.lastScrollPosition = currentScrollPosition;
    },
  },
  mounted() {
    $(window).scroll(this.scrollManNav);
    $(window).scroll(this.scrollAnnounce);
  },
};
</script>
