<template>
  <div>
    <button class="addcart-btn btn" :class="{ 'in-cart': inCart }">
      <!--  -->
      <router-link class="addcart-link" to="/cart" v-if="inCart"> <i class="fas fa-shopping-cart"></i>  </router-link>
      <!--  -->
      <div class="addcart-link" v-else @click.prevent="addToCart()"><i class="fas fa-cart-plus"></i> </div>
    </button>
  </div>
</template>

<script>
export default {
  name: 'AddToCart',
  props: ['course'],
  computed: {
    // 
    inCart() {
      const vm = this;
      return vm.$store.state.cart.cart.find((course) => course.title === vm.course.title);
    },
  },
  methods: {
    addToCart() {
      const vm = this;
      vm.$store.commit('cart/addToCart', vm.course);
      vm.show();
    },
    show() {
      const vm = this;
      vm.$modal.show('addtocart');
    },
  },
};
</script>
