import { db } from '@/firebase/init';
import Vue from 'vue';

export default {
  namespaced: true,
  state() {
    return {
      cat: '',
      status: '',
      course: {},
      courses: [],
      coach: {},
      pagination: {
        totalLength: null,
        itemCount: 7,
        lastItem: null,
        previousFirstItems: [],
      },
    };
  },
  getters: {
    currentPage(state) {
      if (!state.pagination.previousFirstItems) {
        return 1;
      }
      return state.pagination.previousFirstItems.length;
    },
  },
  actions: {
    getPageLength({ commit }) {
      db.collection('courses')
        .get()
        .then((snapshots) => {
          commit('setLength', snapshots.size);
        });
    },

    // 
    getCourses({ commit }, cat) {
      commit('loading', true, {
        root: true,
      });
      commit('setCourses', []);
      let query;
      // 
      if (cat === '') {
        query = db.collection('courses').get();
      } else {
        // 
        query = db.collection('courses').where('categorySelected', '==', cat).get();
      }
      return query.then((snapshots) => {
        const courses = snapshots.docs.map((snapshot) => ({
          ...snapshot.data(),
          id: snapshot.id,
        }));
        commit('setCat', cat);
        commit('setCourses', courses);
        commit('loading', false, {
          root: true,
        });
        return courses;
      });
    },

    // 
    getCourseById({ commit }, courseId) {
      commit('loading', true, {
        root: true,
      });
      commit('setCourse', {});
      db.collection('courses')
        .doc(courseId)
        .get()
        .then(async (snapshot) => {
          const course = snapshot.data();
          course.id = snapshot.id;

          // 
          await db
            .collection('coaches')
            .where('name', '==', course.coach)
            .get()
            .then((snapshots) => {
              // where0
              const coach = snapshots.docs.map((coachProfile) => ({
                ...coachProfile.data(),
              }));
              commit('setCoach', ...coach);
            });

          commit('setCourse', course);
          commit('loading', false, {
            root: true,
          });
        });
    },

    // 
    getCoursesLimit({ commit, state }) {
      commit('loading', true, {
        root: true,
      });
      commit('setCourses', []);
      commit('clearPreviousFirstItem');

      return db
        .collection('courses')
        .orderBy('categorySelected', 'desc')
        .limit(state.pagination.itemCount)
        .get()
        .then((snapshots) => {
          const courses = snapshots.docs.map((doc) => ({
            id: doc.id,
            ...doc.data(),
          }));
          commit('setCourses', courses);
          commit('setLastItem', snapshots.docs[snapshots.docs.length - 1]);
          commit('setPreviousFirstItem', snapshots.docs[0]);
          commit('loading', false, {
            root: true,
          });
          return courses;
        });
    },

    // /
    getMoreCourses({ commit, state }, page) {
      commit('loading', true, {
        root: true,
      });
      let query;

      // 
      if (page === 'next') {
        query = db
          .collection('courses')
          .orderBy('categorySelected', 'desc')
          .startAfter(state.pagination.lastItem)

          .limit(state.pagination.itemCount);
      } else {
        // 
        const lastItemIndex = state.pagination.previousFirstItems.length - 1;
        const previousItem = state.pagination.previousFirstItems[lastItemIndex - 1];

        if (!previousItem) {
          return;
        }

        state.pagination.previousFirstItems.splice(lastItemIndex, 1);
        query = db.collection('courses').orderBy('categorySelected', 'desc').startAt(previousItem).limit(state.pagination.itemCount);
      }

      query.get().then((snapshots) => {
        if (snapshots.docs.length === 0) {
          return [];
        }
        const courses = snapshots.docs.map((doc) => ({
          ...doc.data(),
          id: doc.id,
        }));
        commit('setCourses', courses);
        commit('setLastItem', snapshots.docs[snapshots.docs.length - 1]);
        if (page === 'next') {
          commit('setPreviousFirstItem', snapshots.docs[0]);
        }
        commit('loading', false, {
          root: true,
        });
        return courses;
      });
    },

    // 
    createCourse(_, course) {
      db.collection('courses')
        .add(course)
        .catch((err) => err);
    },

    // 
    removeCourse({ commit }, id) {
      return db
        .collection('courses')
        .doc(id)
        .delete()
        .then(() => {
          commit('setCourseDelete', id);
        });
    },

    // 
    updateCourse({ commit }, { id, course }) {
      if (course && id) {
        db.collection('courses')
          .doc(id)
          .update(course)
          .then(() => {
            commit('setCurCourse', { id, course });
            commit('setCurCourseComment', course);
          });
      }
    },
  },
  mutations: {
    // 
    setCat(state, cat) {
      state.cat = cat;
    },
    // 
    setCourses(state, courses) {
      state.courses = courses;
    },

    // 
    setCourse(state, course) {
      state.course = course;
    },

    // 
    setCoach(state, coach) {
      state.coach = coach;
    },

    // 
    setCurCourse(state, { id, course }) {
      // Vue.set(state.object, key, value) or Vue.set(object, prop, value)
      const index = state.courses.findIndex((item) => item.id === id);
      Vue.set(state.courses, index, course);
    },

    // 

    setCourseDelete(state, id) {
      state.courses = state.courses.filter((course) => course.id !== id);
    },

    // 
    setCurCourseComment(state, course) {
      Vue.set(state.course, 'avgRating', course.avgRating);
    },

    setLastItem(state, course) {
      state.pagination.lastItem = course;
    },
    setPreviousFirstItem(state, course) {
      state.pagination.previousFirstItems.push(course);
    },
    clearPreviousFirstItem(state) {
      state.pagination.previousFirstItems = [];
    },
    setLength(state, length) {
      const totalPage = length / state.pagination.itemCount;
      if (length % state.pagination.itemCount === 0) {
        state.pagination.totalLength = totalPage;
      } else {
        state.pagination.totalLength = Math.ceil(totalPage);
      }
    },
    setStatus(state, status) {
      state.status = status;
    },
  },
};
