import Vue from 'vue';
import Toasted from 'vue-toasted';
import firebase from 'firebase/app';
import 'firebase/auth';
import AOS from 'aos';
import 'aos/dist/aos.css';
import VModal from 'vue-js-modal';
import SocialSharing from 'vue-social-sharing';
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import infiniteScroll from 'vue-infinite-scroll';
import Vuelidate from 'vuelidate';
import Lightbox from 'vue-easy-lightbox';
import currencyFilter from './filters/currencyFilter';
import router from './router';
import store from './store';
import App from './App.vue';

Vue.config.productionTip = false;
Vue.use(Toasted);
Vue.use(VModal, {
  dialog: true,
});
Vue.use(SocialSharing);
Vue.component('Loading', Loading);
Vue.filter('currency', currencyFilter);
Vue.component('AddToCartModal', require('@/components/AddToCartModal.vue').default);
Vue.component('AddToCart', require('@/components/AddToCart.vue').default);
Vue.component('LogRegModal', require('@/components/LogRegModal.vue').default);

Vue.use(infiniteScroll);
Vue.use(Vuelidate);
Vue.use(Lightbox);

const app = null;

// wait for firebase auth to init before creating the app
// 
firebase.auth().onAuthStateChanged(async (user) => {
  if (user) {
    await store.dispatch('auth/storeAuthUser', user);
  }
  // init app if not already created
  if (!app) {
    new Vue({
      created() {
        AOS.init({
          // offset: 200,
          duration: 800,
          easing: 'ease-in-sine',
          delay: 800,
        });
      },
      router,
      store,
      render: (h) => h(App),
    }).$mount('#app');

    router.beforeEach((to, from, next) => {
      const curUser = firebase.auth().currentUser;

      if (to.meta.requiresAuth === true) {
        // 
        if (curUser) {
          // 
          if (to.name === 'Checkout' && from.name !== 'Cart') {
            Vue.toasted.error('', {
              duration: 3000,
            });
            next({
              name: 'Cart',
            });
          } else {
            next();
          }
        } else {
          Vue.toasted.error('', {
            duration: 3000,
          });
          next({
            name: 'home',
          });
        }
      } else {
        next();
      }
    });
  }
});
