import { db } from '@/firebase/init';
import fb from 'firebase/app';
import 'firebase/auth';

export default {
  namespaced: true,
  state() {
    return {
      user: null,
      status: '',
    };
  },
  getters: {
    isAuthenticated(state) {
      return state.user;
    },
  },

  actions: {
    // 
    signUp(_, { email, password }) {
      return fb
        .auth()
        .createUserWithEmailAndPassword(email, password)
        .then(({ user }) => user)
        .catch((error) => {
          let message;
          switch (error.code) {
            case 'auth/email-already-in-use':
              message = ' ';
              break;
            case 'auth/invalid-email':
              message = ' ';
              break;
            case 'auth/weak-password':
              message = ' ';
              break;
            default:
              message = ' ';
          }
          return Promise.reject(message);
        });
    },

    // 
    signIn(_, { email, password }) {
      return fb
        .auth()
        .signInWithEmailAndPassword(email, password)
        .catch((error) => {
          const message = error.code === 'auth/user-not-found' ? ' ' : ' ';
          return Promise.reject(message);
        });
    },

    // 
    signOut({ commit }) {
      return fb
        .auth()
        .signOut()
        .then(() => commit('setAuthUser', null));
    },

    // 
    createUserProfile(_, { uid, userProfile }) {
      return db.collection('users').doc(uid).set(userProfile);
    },

    // 
    storeAuthUser({ commit }, user) {
      return db
        .collection('users')
        .doc(user.uid)
        .get()
        .then((snapshot) => {
          const profile = snapshot.data();
          const theUser = user;
          theUser.profile = profile;
          commit('setAuthUser', theUser);
          return profile;
        });
    },

    // 
    updateUser({ commit }, { id, user }) {
      if (user && id) {
        db.collection('users')
          .doc(id)
          .update(user)
          .then(() => {
            commit('changeUserView', user);
          });
      }
    },
  },

  mutations: {
    setAuthUser(state, user) {
      state.user = user;
    },
    setStatus(state, status) {
      state.status = status;
    },
    changeUserView(state, user) {
      state.user.profile.name = user.name;
      state.user.profile.userImg = user.userImg;
    },
  },
};
